import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const router = new Router({
  linkActiveClass: 'BotBaractive',
  routes: [
    {
      path: '/',
      redirect: '/other/k_line',
    },
    //other
    {
      path: '/other',
      name: 'other',
      component: () => import('@/components/other/index'),
      meta: {
        requireAuth: true,
      },
      children:[
        {
          path: 'k_line',
          name: 'k_line',
          component: () => import('@/components/other/k_line'),
        },
    
       
   
   
      
       
       
        

      ]
    },



  ]
})
window._router = router;
export default router



